import React, { useState, useContext } from 'react';
import { HashRouter as Router,
      Switch,
      Route,
      Redirect,
      Link }
  from 'react-router-dom';
import Home from './pages/home';
import About from './pages/about';
import Login from './pages/login';
import Admin from './pages/admin';
import PageNotFound from './pages/404';
import { UserContext } from './config/userContext';


function App() {

  const [ user, setUser ] = useState(null)
  return (
    <div>

      <Router>
        <div style={{background:"black",
          padding:"10px",
          color:"white"}}>
          
          <Link style={{margin:"10px", color:"white", textDecoration: 'none'}} to="/">Home</Link>
          <Link style={{margin:"10px", color:"white", textDecoration: 'none'}} to="/about">About</Link>
          <Link style={{margin:"10px", color:"white", textDecoration: 'none'}} to="/login">Login</Link>
        </div>
        
        <UserContext.Provider value={{user, setUser}}>
          <Switch>
            <Route exact path="/about" component={About}/>
            <Route exact path="/" component={Home}/>
            <Route exact path="/admin" component={Admin}/>
            <Route exact path="/login" component={Login}/>
            <Route exact path="/404" component={PageNotFound}/>
            <Redirect to="/404"/>
          </Switch>
        </UserContext.Provider>

    </Router>
    </div>
    
    
  );
}


export default App;
